import { Suspense } from "react";
import { Link } from "react-router-dom";
import IndexNavbar from "../../components/Navbar/IndexNavbar";

const FallBack = () => {
  return (
    <div className="flex mx-auto  justify-center mt-36 w-2/3 bg-white py-6 ">
      <h4 className="text-black text-2xl font-bold">Loading ... .. .</h4>
    </div>
  );
};
interface Props {}

const ResetPassword = (props: Props) => {
  const onVerifyPhone = (e: any) => {
    e.preventDefault();
  };

  return (
    <Suspense fallback={<FallBack />}>
      <IndexNavbar />
      <div className="flex justify-center mt-20 md:mt-12 lg:mt-24 ">
        <div className="bg-white rounded-xl w-5/6   md:w-2/6 flex flex-col items-center py-4">
          <span className="block text-3xl">🔐</span>
          <h3 className="font-medium py-3 text-lg">Reset Password</h3>
          <form
            className="mt-4 mb-4 md:mb-4 px-10 "
            onSubmit={(e) => onVerifyPhone(e)}
          >
            <label
              htmlFor="phone number"
              className="inline-block mb-1 font-normal text-sm"
            >
              Phone number
            </label>
            <input
              placeholder="Enter phone number"
              type="text"
              className="flex-grow w-full h-10 text-sm text-gray-600 px-4 mb-2 transition duration-200 bg-white border  border-gray-300 rounded shadow-sm appearance-none focus:border-deep-purple-accent-400 focus:outline-none focus:shadow-outline hover:border-2  hover:border-orange-500 focus:ring-2  focus:ring-orange-500 "
              id="lastName"
              name="lastName"
            />

            <button
              type="submit"
              className="inline-flex justify-center mt-4  items-center  w-full h-10  px-5 font-normal tracking-wide text-white transition duration-200 rounded shadow-md bg-orange-600 hover:bg-white hover:text-orange-600 hover:border-orange-500 hover:border-2 focus:shadow-outline focus:outline-none focus:shadow-none"
            >
              <svg
                className="w-6 h-6 mr-0.5"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M12 11c0 3.517-1.009 6.799-2.753 9.571m-3.44-2.04l.054-.09A13.916 13.916 0 008 11a4 4 0 118 0c0 1.017-.07 2.019-.203 3m-2.118 6.844A21.88 21.88 0 0015.171 17m3.839 1.132c.645-2.266.99-4.659.99-7.132A8 8 0 008 4.07M3 15.364c.64-1.319 1-2.8 1-4.364 0-1.457.39-2.823 1.07-4"
                />
              </svg>
              Verify Phone
            </button>
            <p className="text-xs text-gray-600 sm:text-sm mt-5 ">
              Not Registered ?
              <Link
                to="/register"
                className="text-md font-medium tracking-wide text-orange-500 space-x-0"
              >
                <span> SignUp 😎</span>
              </Link>
            </p>
          </form>
        </div>
      </div>
    </Suspense>
  );
};

export default ResetPassword;
